import {animationStep} from '../visualizerComponent'



// -------------------------------------------------------------------------------------------
export function bubbleSort(arr : number[]){
    var animation : animationStep [] = [];
    let swapped;
    do{
        swapped = false;
        for(let i = 0; i < arr.length-1; i++){
            let step = {bar1 : i, bar2 : i+1 ,swap : 0, bars : [i,i+1]};
            if(arr[i] > arr[i+1]){
                step.swap = 1;
                let temp = arr[i];
                arr[i] = arr[i+1];
                arr[i+1] = temp;
                swapped = true;
            }
            animation.push(step);
        }
    }while(swapped)
    
    return animation;
}
// -------------------------------------------------------------------------------------------

//https://en.wikipedia.org/wiki/Insertion_sort
// -------------------------------------------------------------------------------------------
export function insertionSort(arr:number[]){
    var animation : animationStep [] = [];

    let idx = 1;
    while (idx < arr.length){
        let j = idx;
        let step = {bar1 : idx, bar2 : j ,swap : 0, bars : [idx,j]};
        animation.push(step)
        
        while(j > 0 && arr[j-1] > arr[j]){
            let temp = arr[j];
            arr[j] = arr[j-1];
            arr[j-1] = temp;
            let step = {bar1 : j, bar2 : j-1 ,swap : 1, bars : [j,j-1]};
            animation.push(step)
            j--;
        }
        idx++;
    }
    return animation;
}
// -------------------------------------------------------------------------------------------

//https://en.wikipedia.org/wiki/Selection_sort
// -------------------------------------------------------------------------------------------
export function selectionSort(arr:number[]){
    var animation : animationStep [] = [];

    for(let i = 0; i < arr.length-1;i++){
        let min = i;
        for(let z = i +1; z < arr.length;z++){
            if(arr[z] < arr[min])
                min = z
            let step = {bar1 : i, bar2 : z ,swap : 0, bars : [i,z]};
            animation.push(step)
        }
        if(min !== i){
            let temp = arr[min];
            arr[min] = arr[i];
            arr[i] = temp;
            let step = {bar1 : min, bar2 : i ,swap : 1, bars : [i,min]};
            animation.push(step)
        }
    }
    return animation;
}
// -------------------------------------------------------------------------------------------

//https://en.wikipedia.org/wiki/Gnome_sort
// -------------------------------------------------------------------------------------------
export function gnomeSort (arr:number[]){
    var animation : animationStep [] = [];
    let pos = 0;
    while (pos < arr.length){
        let step = {bar1 : pos, bar2 : pos-1 ,swap : 0, bars : [pos,pos-1 >= 0 ? pos-1 : pos]};        
        if (pos === 0 || arr[pos] >= arr[pos-1])
            pos++;
        else{
            let temp = arr[pos];
            arr[pos] = arr[pos-1];
            arr[pos-1] = temp;
            pos--;
            step.swap = 1;
        }
        animation.push(step);
    }
    return animation;
}
