//https://en.wikipedia.org/wiki/Shellsort
// -------------------------------------------------------------------------------------------
export function shellSort(arr:number[]){
    var animation : animationStep [] = [];
    for(let gap = Math.floor(arr.length/2); gap > 0 ;){
        for(let i = gap; i < arr.length;i++){
        
            let temp = arr[i]
            let j
            for( j = i; j >= gap && arr[j-gap] > temp ; j-=gap){
                arr[j] = arr[j-gap];
                let step = {bar1 : j, bar2 : j-gap,swap:0, setValue : arr[j-gap], bars : [j,j-gap]};        
                animation.push(step)
            }
            arr[j] = temp;
            let step = {bar1 : j, bar2 : i,swap:0, setValue : temp, bars : [j]};        
            animation.push(step)
        }
        gap = Math.floor(gap/2);
    }
    return animation;
}
