//https://en.wikipedia.org/wiki/Quicksort
// -------------------------------------------------------------------------------------------
export function quickSort(arr:number[],low :number, high :number){
    var animation : animationStep [] = [];
    runQuickSort(arr,low,high,animation)
    return animation;
}

function runQuickSort(arr:number[],low :number, high :number,animation : animationStep[]){
    if(low < high){
        let p : number = partition(arr,low,high,animation);
        runQuickSort(arr,low,p,animation);
        runQuickSort(arr,p+1,high,animation); 
    }
}
// -------------------------------------------------------------------------------------------


//https://www.geeksforgeeks.org/hoares-vs-lomuto-partition-scheme-quicksort/
//Using hoares
function partition(arr:number[],low:number,high:number,animation:animationStep[]){
    // let pivot = arr[low]; 
    let index = Math.floor(low + (high - low) / 2);
    let pivot = arr[index];
    let i = low - 1;
    let j = high + 1;
    while(true){
        do{
            let step = {bar1 : i, bar2 : j ,swap : 0, bars : [i,j,index]};
            animation.push(step);
            i++;
        }while(arr[i] < pivot)

        do{
            let step = {bar1 : i, bar2 : j ,swap : 0, bars : [i,j,index]};
            animation.push(step);        
            j--;
        }while(arr[j] > pivot)
        

        let step = {bar1 : i, bar2 : j ,swap : 0, bars : [i,j,index]};
        if(i >= j){
            animation.push(step);  
            return j
        }
        let temp = arr[i];
        arr[i] = arr[j];
        arr[j] = temp;
        step.swap = 1;
        animation.push(step);  
        
    }
}

