import React from 'react';
import './visualizerComponent.css'
import {
  bubbleSort, 
  insertionSort,
  selectionSort,
  gnomeSort,
  shellSort,
  quickSort
} from './sortingAlgorithms/algorithms'



export interface Props{
  
}

interface settings {
  maxValue: number;
  minValue: number;
  arraySize: number;
  barColor: string;
  activeBarColor: string;
  backgroundColor : string;
  animationSpeed: number
}

interface algorithmStats{
  steps: number;
  currentStep: number;
  currentAlgorithm : string;
}

export interface animationStep{
  bar1: number;
  bar2: number;
  bars: number[];
  swap?: number;
  setValue? : number;
}



export default class VisualizerComponent extends React.Component<
  {},  
  {
    array : number[],
    timeouts : any[],
    isSorted : boolean,
    settings : settings,
    stats : algorithmStats,
    maxArraySize : number
  }>
  
  {
    private animation : animationStep [] = [];

    constructor(props:Props) {
        super(props);
        this.state = {
          array : [],
          timeouts : [],
          isSorted : false,
          maxArraySize : Math.round(window.innerWidth  / 6),
          settings : {
            maxValue : Math.round(window.innerHeight - 60),
            minValue : 25,
            arraySize: Math.round(window.innerWidth  / 8),
            barColor: "#0080ff",
            activeBarColor: "#ff0000",
            backgroundColor: "#282c34",
            animationSpeed: 1,
          },
          stats:{
            steps: 0,
            currentStep: 0,
            currentAlgorithm: "",
          }
        }
    }

    componentDidMount(){
      this.setState({array:this.randomizeArray()})

      window.addEventListener('resize',this.updateResize)
    }
   

    updateResize= () =>{
      let new_settings = this.state.settings
      new_settings.maxValue = Math.round(window.innerHeight - 60)
      this.setState({maxArraySize : Math.round(window.innerWidth  / 6),settings:new_settings})
    }


    //Randomize the values in array
    randomizeArray(){
      let newArray : number[] = [];
      for(let i = 0; i < this.state.settings.arraySize;i++){
        newArray.push(Math.floor(Math.random() * (this.state.settings.maxValue - this.state.settings.minValue)) + this.state.settings.minValue)
      }
      return newArray
    }
  
    resetArray = () => {
      while(this.state.timeouts.length !== 0){
        clearTimeout(this.state.timeouts[0])
        this.state.timeouts.shift();
      }
      this.resetBars()
      this.setState({array:this.randomizeArray(),isSorted:false})
    }

    resetBars = () => {
      let arraybars = document.getElementsByClassName("array-bar") as HTMLCollectionOf<HTMLElement>;
      for(let i = 0; i < arraybars.length;i++){
        arraybars[i].style.backgroundColor = this.state.settings.barColor;
      }
    }

    runAlgorithm = (algorithm : (arr:number[]) => any, algorithmName : string) =>{
      let copy = [...this.state.array]
      if(this.state.timeouts.length === 0){
        this.animation = algorithm(copy)
        let new_stats = this.state.stats
        new_stats.steps = this.animation.length
        new_stats.currentAlgorithm = algorithmName
        new_stats.currentStep = 0
        this.runAnimation()
        this.setState({isSorted:true,stats : new_stats})
      }
    }

    runAnimation(){
      let prevBar1 : number = -1;
      let prevBar2 : number = -1;
      let arraybars = document.getElementsByClassName("array-bar") as HTMLCollectionOf<HTMLElement>;
      for(let i = 0; i < this.animation.length;i++){
        this.state.timeouts.push(setTimeout(()=>{
          if(prevBar1 > -1 && prevBar1 < this.state.array.length) arraybars[prevBar1].style.backgroundColor = this.state.settings.barColor;
          if(prevBar2 > -1 && prevBar2 < this.state.array.length) arraybars[prevBar2].style.backgroundColor = this.state.settings.barColor;
          this.animation[i].bars.forEach((barID,idx) => {
            if(barID < 0 || barID >= this.state.array.length || barID === undefined){
              return;
            }

            if(idx === 2){
              //Pivot
              arraybars[barID].style.backgroundColor = "rgb("+(255).toString()+",255,255)";
            }else{
              arraybars[barID].style.backgroundColor = this.state.settings.activeBarColor
            }
          });
           
          //Just set value
          if(this.animation[i].setValue !== undefined && this.animation[i].setValue! >= 0){
            let newArray = this.state.array
            newArray[this.animation[i].bar1] = this.animation[i].setValue!
            this.setState({array : newArray})
          }else if(this.animation[i].swap === 1){ //Do swap
            let newArray = this.state.array
            let temp = newArray[this.animation[i].bar1]
            newArray[this.animation[i].bar1] = newArray[this.animation[i].bar2]
            newArray[this.animation[i].bar2] = temp
            this.setState({array : newArray})
          }
          
          prevBar1 = this.animation[i].bar1
          prevBar2 = this.animation[i].bar2       

          this.state.timeouts.shift();
          let new_stats = this.state.stats
          new_stats.currentStep++
          this.setState({stats : new_stats})
          if(this.state.timeouts.length <= 0){
            this.setState({isSorted:true})
          }
        }, i * this.state.settings.animationSpeed))
      }

    }

   


    render(){

      var list : number[] = this.state.array;
      const pColor : string = this.state.settings.barColor;
      let copy = [...this.state.array]

      return <div>
        <div className = "array-container" 
        style={{backgroundColor: `${this.state.settings.backgroundColor}`}}>
          {list.map((val,idx)=>(
            <div
              className = "array-bar"
              key = {idx}
              id = {idx.toString()}
              style = {{
                backgroundColor :`${pColor}`,
                height : `${val}px`
              }}></div>
          ))}
          </div>


        <div className = "button-container">

          <div className="dropup">
            <button className="dropbtn">Algorithms ^</button>
              <div className="dropup-content">
                  <button 
                    disabled = {this.state.isSorted} 
                    className="buttons" 
                    onClick={() => {
                      this.runAlgorithm(bubbleSort,"Bubble Sort")
                  }}>Bubble Sort</button>
                    
                  <button 
                    disabled = {this.state.isSorted} 
                    className="buttons" 
                    onClick={() => {
                      this.runAlgorithm(insertionSort,"Insertion Sort")
                  }}>Insertion Sort</button>

                  <button 
                    disabled = {this.state.isSorted} 
                    className="buttons" 
                    onClick={() => {
                      this.runAlgorithm(selectionSort,"Selection Sort")
                  }}>Selection Sort</button>

                   <button 
                    disabled = {this.state.isSorted} 
                    className="buttons" 
                    onClick={() => {
                      this.runAlgorithm(gnomeSort,"Gnome Sort")
                  }}>Gnome Sort</button>

                  <button 
                    disabled = {this.state.isSorted} 
                    className="buttons" 
                    onClick={() => {
                      this.runAlgorithm(shellSort,"Shell Sort")
                  }}>Shell Sort</button>

                   <button 
                    disabled = {this.state.isSorted} 
                    className="buttons" 
                    onClick={() => {
                      if(this.state.timeouts.length === 0){
                        this.animation = quickSort(copy,0,copy.length-1)
                        let new_stats = this.state.stats
                        new_stats.steps = this.animation.length
                        new_stats.currentAlgorithm = "Quick Sort"
                        new_stats.currentStep = 0
                        this.runAnimation()
                        this.setState({isSorted:true,stats : new_stats})
                      }
                  }}>Quicksort</button>
                
              </div> {/*dropup-content*/}
            </div> {/*Dropup*/}
 
              {/* Settings */}
            <div className="settings">

                  {/* Animation speed */}
                  <div className="numberDivs">
                    <label >Delay Speed(MS): </label>
                      <input 
                        type="number" 
                        min = ".1" 
                        max= {Number.MAX_SAFE_INTEGER }
                        value={this.state.settings.animationSpeed} 
                        style= {{width:"40px"}}
                        onChange = {(e)=>{
                          let val = parseFloat(e.target.value) 
                          if(val > .1 && val <= Number.MAX_SAFE_INTEGER){
                            let new_settings = this.state.settings
                            new_settings.animationSpeed = val 
                            this.setState({settings: new_settings})
                          }
                        }}
                      ></input>
                  </div>

                  {/* Array Size */}
                  <div className="numberDivs">
                    <label >Array Size: </label>
                      <input 
                        type="number" 
                        min = "1" 
                        max= {this.state.maxArraySize.toString()}
                        pattern =".{0,3}"
                        value={this.state.settings.arraySize} 
                        style= {{width:"40px"}}
                        onChange = {(e)=>{
                          let value = parseFloat(e.target.value) 
                          if(value >= 1 && value <= this.state.maxArraySize){
                            let new_settings = this.state.settings
                            new_settings.arraySize = value
                            this.setState({settings: new_settings})
                            this.resetArray()
                          }
                        }}
                      ></input>
                  </div>


                    {/* Bar colors */}
                  <div id="colors">
                    <label>Bar colors: </label>
                    <input 
                      type="color" 
                      style={{display:"block"}}
                      value = {this.state.settings.barColor}
                      onChange = {(e)=>{
                        let new_settings = this.state.settings
                        new_settings.barColor = e.target.value      
                        this.setState({settings: new_settings})                          
                      }}
                    ></input>
                  </div>

                  {/* highlight color */}
                  <div id="colors">
                    <label>Highlight Color: </label>
                    <input 
                      type="color" 
                      style={{display:"block"}}
                      value = {this.state.settings.activeBarColor}
                      onChange = {(e)=>{
                        let new_settings = this.state.settings
                        new_settings.activeBarColor = e.target.value      
                        this.setState({settings: new_settings})                          
                      }}
                    ></input>
                  </div>

                   {/* Background color */}
                   <div id="colors">
                    <label>BG Color: </label>
                    <input 
                      type="color" 
                      style={{display:"block"}}
                      value = {this.state.settings.backgroundColor}
                      onChange = {(e)=>{
                        let new_settings = this.state.settings
                        new_settings.backgroundColor = e.target.value      
                        this.setState({settings: new_settings})                          
                      }}
                    ></input>
                  </div>


                 
                
                  <div className ="statDiv">
                      <label className="stats">Current Algorithm : <span style={{color:"#32cd32"}}>{this.state.stats.currentAlgorithm}</span></label>
                      <label className="stats" >Total Steps: {this.state.stats.steps}</label>
                      <label className="stats" >Current Steps: {this.state.stats.currentStep}</label>
                  </div>

                
            </div>
            
            <button id="resetBtn" onClick={this.resetArray}>Reset Array</button>
          </div>{/* Button Container */} 

      </div>
    }
}